<template>
  <div class="dashboard">
    <!-- Sidebar -->
    <aside :class="['sidebar', { 'sidebar-expanded': isSidebarOpen }]">
      <div class="sidebar-inner">
        <div class="logo-container" @click="toggleSidebar">
          <img src="/LogoN.png" alt="Logo" class="w-12 h-12 object-contain" />
        </div> 
        <!-- Expanded Sidebar Content -->
        <div class="sidebar-content" v-show="isSidebarOpen">
          <h2 class="sidebar-title">RESIDENCIAS ITMH</h2>
          <nav class="sidebar-nav">
            <a href="#" class="nav-link" @click="setActiveView('home')">
              <span class="nav-text">Inicio</span>
            </a>
            <a href="#" class="nav-link" @click="setActiveView('project')">
              <span class="nav-text">Mi Proyecto</span>
            </a>
            <a href="#" class="nav-link" @click="setActiveView('documents')">
              <span class="nav-text">Documentos</span>
            </a>
            <a href="#" class="nav-link" @click="setActiveView('notifications')">
              <span class="nav-text">Notificaciones</span>
            </a>
            <a href="#" class="nav-link" @click="setActiveView('account')">
              <span class="nav-text">Mi Cuenta</span>
            </a>
          </nav>
        </div>
      </div>
    </aside>

    <!-- Main Content -->
    <main class="main-content">
      <div class="top-bar">
        <h1 class="page-title">{{ pageTitle }}</h1>
        <button @click="handleLogout" class="logout-button">Cerrar Sesión</button>
      </div>

      <!-- Welcome Screen -->
      <div v-if="activeView === 'home'" class="welcome-screen">
        <div class="welcome-content">
          <h1 class="welcome-title">Bienvenido Alumno</h1>
          <h2 class="welcome-subtitle">TECNM MATEHUALA</h2>
        </div>
      </div>

      <!-- Project View -->
      <div v-else-if="activeView === 'project'">
        <div class="project-status">
          <h2 class="section-title">Estado del Proyecto</h2>
          <p>Progreso general: {{ projectProgress }}%</p>
          <div class="progress-bar">
            <div class="progress" :style="{ width: `${projectProgress}%` }"></div>
          </div>
        </div>

        <div class="document-section">
          <h2 class="section-title">Documentos del Proyecto</h2>
          <div class="document-grid">
            <div v-for="doc in documents" :key="doc.id" class="document-card">
              <h3 class="document-title">{{ doc.title }}</h3>
              <p class="document-status" :class="doc.status">{{ doc.status }}</p>
              <input 
                v-if="doc.status === 'Pendiente'"
                type="file" 
                @change="uploadDocument($event, doc.id)" 
                class="file-input"
              />
              <button 
                v-if="doc.status === 'Revisión' || doc.status === 'Aprobado'"
                @click="downloadDocument(doc.id)" 
                class="download-button"
              >
                Descargar
              </button>
            </div>
          </div>
        </div>
      </div>

      <!-- Placeholder for other views -->
      <div v-else-if="activeView === 'documents'" class="placeholder-view">
        <p>Aquí podrás ver todos tus documentos.</p>
      </div>

      <div v-else-if="activeView === 'notifications'" class="placeholder-view">
        <p>Aquí aparecerán tus notificaciones.</p>
      </div>

      <div v-else-if="activeView === 'account'" class="placeholder-view">
        <p>Aquí podrás gestionar tu cuenta.</p>
      </div>
    </main>
  </div>
</template>

<script setup>
import { ref, computed } from 'vue'
import { useRouter } from 'vue-router'

const router = useRouter()
const isSidebarOpen = ref(false)
const activeView = ref('home')

const toggleSidebar = () => {
  isSidebarOpen.value = !isSidebarOpen.value
}

const setActiveView = (view) => {
  activeView.value = view
}

const pageTitle = computed(() => {
  switch (activeView.value) {
    case 'home': return 'Inicio'
    case 'project': return 'Mi Proyecto de Residencias'
    case 'documents': return 'Documentos'
    case 'notifications': return 'Notificaciones'
    case 'account': return 'Mi Cuenta'
    default: return ''
  }
})

const documents = ref([
  { id: 1, title: 'Anteproyecto', status: 'Aprobado' },
  { id: 2, title: 'Reporte Preliminar', status: 'Revisión' },
  { id: 3, title: 'Reporte Final', status: 'Pendiente' },
  { id: 4, title: 'Presentación', status: 'Pendiente' },
])

const projectProgress = computed(() => {
  const totalDocs = documents.value.length
  const completedDocs = documents.value.filter(doc => doc.status === 'Aprobado').length
  return Math.round((completedDocs / totalDocs) * 100)
})

const uploadDocument = (event, docId) => {
  const file = event.target.files[0]
  if (file) {
    console.log(`Uploading ${file.name} for document ID ${docId}`)
    const docIndex = documents.value.findIndex(doc => doc.id === docId)
    if (docIndex !== -1) {
      documents.value[docIndex].status = 'Revisión'
    }
  }
}

const downloadDocument = (docId) => {
  console.log(`Downloading document with ID ${docId}`)
}

const handleLogout = () => {
  console.log('Logout clicked')
  router.push('/')
}
</script>

<style scoped>
.dashboard {
  display: flex;
  min-height: 100vh;
  background-color: #f8fafc;
}

.sidebar {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  width: 80px;
  z-index: 50;
  transition: width 0.3s ease;
}

.sidebar-inner {
  height: 100%;
  background: linear-gradient(180deg, #1e3a8a 0%, #1e40af 100%);
  padding: 1.5rem;
  display: flex;
  flex-direction: column;
}

.sidebar-expanded {
  width: 250px;
}

.logo-container {
  cursor: pointer;
  transition: transform 0.2s;
  margin-bottom: 1rem;
}

.logo-container:hover {
  transform: scale(1.1);
}

.logo-container img {
  width: 48px;
  height: 48px;
  object-fit: contain;
}

.sidebar-content {
  display: none;
  flex-direction: column;
  flex-grow: 1;
  overflow-y: auto;
}

.sidebar-expanded .sidebar-content {
  display: flex;
}

.sidebar-title {
  color: white;
  font-size: 1.25rem;
  font-weight: 600;
  margin-bottom: 2rem;
  padding: 0 0.5rem;
}

.sidebar-nav {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.nav-link {
  display: flex;
  align-items: center;
  padding: 0.75rem 1rem;
  color: white;
  text-decoration: none;
  border-radius: 0.5rem;
  transition: background-color 0.2s;
}

.nav-link:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

.nav-text {
  font-size: 0.875rem;
  font-weight: 500;
}

.main-content {
  flex: 1;
  margin-left: 80px;
  padding: 2rem;
  transition: margin-left 0.3s ease;
}

.sidebar-expanded + .main-content {
  margin-left: 250px;
}

.top-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}

.page-title {
  font-size: 2rem;
  font-weight: 700;
  color: #1e3a8a;
}

.logout-button {
  padding: 0.5rem 1rem;
  background-color: #ef4444;
  color: white;
  border: none;
  border-radius: 0.25rem;
  cursor: pointer;
  transition: background-color 0.2s;
}

.logout-button:hover {
  background-color: #dc2626;
}

.welcome-screen {
  height: calc(100vh - 100px);
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url('@/assets/LOGO.png');
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  background-color: #f8fafc;
}

.welcome-content {
  text-align: center;
  color: #1e3a8a;
  background-color: rgba(255, 255, 255, 0.8);
  padding: 2rem;
  border-radius: 1rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.welcome-title {
  font-size: 3rem;
  font-weight: 700;
  margin-bottom: 1rem;
}

.welcome-subtitle {
  font-size: 2rem;
  font-weight: 500;
}

.section-title {
  font-size: 1.5rem;
  font-weight: 600;
  color: #1e3a8a;
  margin-bottom: 1rem;
}

.project-status {
  background-color: white;
  padding: 1.5rem;
  border-radius: 0.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  margin-bottom: 2rem;
}

.progress-bar {
  width: 100%;
  height: 1rem;
  background-color: #e2e8f0;
  border-radius: 0.5rem;
  overflow: hidden;
}

.progress {
  height: 100%;
  background-color: #3b82f6;
  transition: width 0.3s ease;
}

.document-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1.5rem;
}

.document-card {
  background-color: white;
  padding: 1.5rem;
  border-radius: 0.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
}

.document-title {
  font-size: 1.25rem;
  font-weight: 600;
  color: #1e3a8a;
  margin-bottom: 0.5rem;
}

.document-status {
  font-weight: 500;
  margin-bottom: 1rem;
}

.document-status.Aprobado { color: #10b981; }
.document-status.Revisión { color: #f59e0b; }
.document-status.Pendiente { color: #ef4444; }

.file-input, .download-button {
  width: 100%;
  padding: 0.5rem;
  border: 1px solid #e2e8f0;
  border-radius: 0.25rem;
  font-size: 0.875rem;
  cursor: pointer;
}

.download-button {
  background-color: #3b82f6;
  color: white;
  border: none;
  transition: background-color 0.2s;
}

.download-button:hover {
  background-color: #2563eb;
}

.placeholder-view {
  background-color: white;
  border: 1px solid #e2e8f0;
  border-radius: 0.5rem;
  padding: 2rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
}

@media (max-width: 768px) {
  .sidebar {
    width: 60px;
  }

  .sidebar-expanded {
    width: 200px;
  }

  .main-content {
    margin-left: 60px;
  }

  .sidebar-expanded + .main-content {
    margin-left: 200px;
  }
}
</style>