<template>
  <div class="login-container">
    <!-- Left Section -->
    <div class="left-section">
      <div class="logo-content">
        <img src="/LogoN.png" alt="ITMH Logo" class="logo" />
        <h1 class="main-title">BIENVENIDO A RESIDENCIAS ITMH</h1>
        <p class="subtitle">
          Solo para estudiantes del Instituto Tecnológico de Matehuala.
        </p>
        <p class="description">
          Conéctate con oportunidades de residencias profesionales, registra tu proyecto y haz el seguimiento desde un solo lugar.
        </p>
      </div>
    </div>

    <!-- Right Section -->
    <div class="right-section">
      <div class="login-form">
        <p class="welcome-text">Bienvenido alumn@ a Residencias ITMH</p>
        <h2 class="login-title">¡Inicia Sesión!</h2>

        <form @submit.prevent="handleLogin">
          <div class="form-group">
            <label>Ingresa tu correo institucional</label>
            <input
              type="email"
              v-model="email"
              placeholder="Ingresa tu correo electronico"
              required
            />
          </div>

          <div class="form-group">
            <label>Ingresa tu contraseña</label>
            <input
              type="password"
              v-model="password"
              placeholder="Contraseña"
              required
            />
          </div>

          <div class="form-links">
            <a href="#" class="forgot-password">Olvidaste tu contraseña?</a>
            <div class="signup-prompt">
              No tienes cuenta?
              <a href="#" class="signup-link">Sign up</a>
            </div>
          </div>

          <button type="submit" class="login-button">
            Inicia Sesión
          </button>
        </form>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref } from 'vue'
import { useRouter } from 'vue-router'
import { useAuth } from '../router'

const email = ref('')
const password = ref('')
const router = useRouter()
const { login } = useAuth()

const handleLogin = () => {
  if (isValidEmail(email.value)) {
    if (login(email.value, password.value)) {
      // Updated paths to match your component structure
      if (isStudent(email.value)) {
        router.push('/components/AlumnoDashboard')
      } else {
        router.push('/components/DashboardView')
      }
    } else {
      alert('Credenciales incorrectas')
    }
  } else {
    alert('Por favor, ingresa un correo válido de ITMH')
  }
}

const isValidEmail = (email) => {
  const domain = '@matehuala.tecnm.mx'
  return email.toLowerCase().endsWith(domain)
}

const isStudent = (email) => {
  const studentPattern = /^[Ll]\d{8}@matehuala.tecnm.mx$/
  return studentPattern.test(email)
}
</script>


