import { createRouter, createWebHistory } from 'vue-router'
import { ref } from 'vue'
import LoginPage from '../views/LoginPage.vue'
import AlumnoDashboard from '../components/AlumnoDashboard.vue'
import DashboardView from '../components/DocenteDashboard.vue'

const isAuthenticated = ref(false)

const routes = [
  {
    path: '/',
    name: 'Login',
    component: LoginPage
  },
  {
    path: '/components/AlumnoDashboard',
    name: 'AlumnoDashboard',
    component: AlumnoDashboard
  },
  {
    path: '/components/DashboardView',
    name: 'DashboardView',
    component: DashboardView
  }
    
]

const router = createRouter({
  history: createWebHistory(),
  routes
})

router.beforeEach((to, from, next) => {
  if (to.meta.requiresAuth && !isAuthenticated.value) {
    next('/')
  } else {
    next()
  }
})

export function useAuth() {
  const login = (email, password) => {
    // Simulación de login
    if (email && password) {
      isAuthenticated.value = true
      return true
    }
    return false
  }

  const logout = () => {
    isAuthenticated.value = false
  }

  return {
    isAuthenticated,
    login,
    logout
  }
}

export default router